"use strict";
exports.__esModule = true;
/// <reference path="../typings/index.d.ts" />
var angular = require("angular");
require("angular-ui-router");
require("angular-resource");
require("angular-cookies");
require("ngmap");
var routes_1 = require("./routes");
var engMap_component_1 = require("./app/components/engMap.component");
var eline_side_component_1 = require("./app/components/eline-side.component");
angular.module('vnaasgui', [
    'ui.router',
    'app',
    'ngMap'
])
    .config(routes_1["default"])
    .component('engMap', engMap_component_1.engMap)
    .component('elineSide', eline_side_component_1.elineSide)
    .run(function ($log, $state, XosNavigationService, XosComponentInjector) {
    $log.info('[vnaasgui] App is running');
    XosNavigationService.add({
        label: 'vNaaS GUI',
        state: 'xos.vnaasgui'
    });
});



